import React, { useState, useEffect } from 'react'
import { UPDATE_TODO } from '../../queries'

const updateAfterDeleteMutation = (cache, { data }) => {
  ;(function(cache, data) {
    const { todoes } = cache.readQuery({ query: GET_TODOS })
    cache.writeQuery({
      query: GET_TODOS,
      data: { todoes: todoes.filter(todo => todo.id !== data.deleteTodo.id) }
    })
  })(cache, data)
}

const Note = ({ deleteMethod, toggleMethod, updateMethod, text, completed }) => {
  let inputRef

  const [editable, setEditable] = useState(false)
  const [value, setValue] = useState(text)

  const [deleteTodo] = useMutation(DELETE_TODO, { update: updateAfterDeleteMutation })
  const [toggleTodo] = useMutation(TOGGLE_TODO)
  const [updateTodo] = useMutation(UPDATE_TODO)

  useEffect(() => {
    if (editable) {
      inputRef.focus()
    }
  }, [editable])

  const handleEdit = () => {
    setEditable(!editable)
  }

  const classString = completed ? 'note completed' : 'note'
  return (
    <div className={classString}>
      <input
        ref={input => (inputRef = input)}
        className={editable ? 'editable' : ''}
        type="text"
        value={value}
        disabled={!editable}
      />
      <button className="edit" onClick={handleEdit}>
        {editable ? 'Save' : 'Edit'}
      </button>
      <button className="done" onClick={() => toggleMethod()}>
        {completed ? 'Undone' : 'Done'}
      </button>
      <button className="delete" onClick={() => deleteMethod()}>
        Delete
      </button>
    </div>
  )
}

export default Note
